    display: flex; }
    .header-body .menu-header ul {
      list-style: none;
      padding: 0 20px;
      margin: 0; }
      .header-body .menu-header ul a {
        display: inline-block;
        font-family: PTSans;
        color: #ffffff;
        font-size: 14px;
        font-weight: 400;
        padding-bottom: 5px;
        transition: 1s;
        position: relative; }
        .header-body .menu-header ul a:after {
          content: '';
          position: absolute;
